import React from "react";
import { NavLink } from "react-router-dom";
//import web from "../src/images/home.svg"
import './index.css'
const Common = (props) => {
  return (
    <>
      <section id="header" className="d-flex align-items-center">
        <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
               <div className="row ">
                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                    <h1> 
                       <strong> {props.name}</strong> <strong className="brand-name">AIRbots</strong>
                    </h1>
                    <h2 className="my-3">
                    An emerging startup that seeks to provide automation and robotics solutions by providing products which accelerates business development and is economically viable even for small business
                    </h2>
                    <div className="mt-3" style={{padding:'5%'}}>
                        <NavLink to={props.visit} className="btn-get-started">
                          {props.btname}
                        </NavLink>
                    </div>
                </div>

                <div className="col-lg-6 order1 order-lg-2 header-mg">
                    <img src={props.imgsrc} className="img-fluid animated " alt="img"/>
                </div>
                </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Common;
